.list {
  display: grid;
  grid-row-gap: 20px;
  width: 100%;

  @include breakpoint(tablet) {
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
  }

  @include breakpoint(desktop) {
    grid-template-columns: repeat(4, 1fr);
    max-height: calc(100% - 80px);
    padding: 10px 3px;
    overflow: auto;
  }

  &__noresult {
    grid-column: 1/3;
    margin-bottom: 20px;
    color: var(--theme);
    font-family: "Montserrat", sans-serif;
    font-size: 2.4rem;
    font-weight: 600;
  }
}
