.card {
  position: relative;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, .2);

  &__figure {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__image {
    width: 200px;

    @include breakpoint(desktop) {
      width: 100%;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 120px;
    margin-top: 10px;
    padding: 0 20px;
    color: $onyx;
    background-color: $snow;
    border-radius: 10px;
    font-family: $textFont;
  }

  &__title {
    font-size: 2rem;
    font-weight: 600;
    text-transform: capitalize;
  }

  &__value {
    margin-top: 5px;
    font-size: 1.4rem;
    font-weight: 400;
  }

  &__button {
    position: absolute;
    right: 20px;
    bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: var(--theme);
    border: 0;
    border-radius: 50px;
    box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, .2);
    transform: translate(-1px, -1px);
    transition: .2s;

    &:active {
      box-shadow: none;
      transform: translate(0, 0);
    }
  }

  &__icon {
    position: relative;
    display: block;
    width: 15px;
    height: 2px;
    background-color: white;

    &::after {
      position: absolute;
      display: block;
      content: '';
      width: 15px;
      height: 2px;
      background-color: white;
      transform: rotate(90deg);
    }
  }
}
